import React, { Component } from "react";
import { Dropdown } from "react-bootstrap";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  disconnectUser,
  selectAccount,
} from "../../../../Services/Actions/addFeedAction";
import ProfileImage from "../../CommonComponents/ProfileImage/ProfileImage";

class AccountPicker extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedAccount: props.connectedAcc[0],
      accounts: [],
    };
  }

  componentDidMount() {
    const { selectAccount, personalAccount, personal, connectedAcc } =
      this.props;
    if (connectedAcc && connectedAcc.length) {
      this.setState(
        {
          accounts: personal ? personalAccount : connectedAcc,
          selectedAccount: personal
            ? personalAccount.length > 0
              ? personalAccount[0]
              : null
            : connectedAcc[0],
        },
        () => {
          const { selectedAccount } = this.state;
          if (selectedAccount && Object.keys(selectedAccount).length > 0) {
            this.props.onConnectedAccountChange(
              this.state.selectedAccount.ConnectedAccounts.id
            );
            this.props.selectAccount(
              this.state.selectedAccount.ConnectedAccounts.id
            );
          }
        }
      );
    }
  }

  componentWillReceiveProps(nextProps) {
    const { connectedAcc, personal, personalAccount } = nextProps;
    if (
      this.props.connectedAcc != nextProps.connectedAcc ||
      this.props.tab != nextProps.tab
    ) {
      this.setState(
        {
          accounts: personal ? personalAccount : connectedAcc,
          selectedAccount: personal
            ? personalAccount.length > 0
              ? personalAccount[0]
              : null
            : connectedAcc[0],
        },
        () => {
          const { selectedAccount } = this.state;
          if (selectedAccount && Object.keys(selectedAccount).length > 0) {
            this.props.onConnectedAccountChange(
              this.state.selectedAccount.ConnectedAccounts.id
            );
            this.props.selectAccount(
              this.state.selectedAccount.ConnectedAccounts.id
            );
          }
        }
      );
    }
  }

  disconnectAccount = (account) => {
    const { accounts } = this.state;

    this.props.disconnectAccount(account.ConnectedAccounts.id, () => {
      if (accounts.length - 1 >= 1) {
        const updateAccountsList = accounts.filter(
          (acc) => acc.ConnectedAccounts.id !== account.ConnectedAccounts.id
        );
        this.props.onConnectedAccountChange(
          updateAccountsList[0].ConnectedAccounts.id
        );
        this.props.selectAccount(updateAccountsList[0].ConnectedAccounts.id);
        this.setState({
          accounts: updateAccountsList,
          selectedAccount: updateAccountsList[0],
        });
      } else {
        this.props.selectAccount(null);
        this.props.onConnectedAccountChange(null);
      }
    });
  };

  deleteAccount = (account) => {
    Swal.fire({
      title: "<strong>Confirm</strong>",
      icon: "warning",
      text: "Do you want to disconnect account?",
      showCloseButton: true,
      showCancelButton: true,
      focusConfirm: true,
      confirmButtonText: "Yes",
      confirmButtonAriaLabel: "Yes",
      cancelButtonAriaLabel: "Thumbs down",
    }).then((result) => {
      if (result.value) {
        this.disconnectAccount(account);
      } else if (result.dismiss === "cancel") {
      }
    });
  };

  onAccountChange = (id) => {
    this.props.selectAccount(id ? id : null);
    this.props.onConnectedAccountChange(id);
    if (!id) {
      this.setState({ selectedAccount: null });
    }
  };

  render() {
    const { selectedAccount, accounts } = this.state;
    return (
      <>
        {accounts && accounts.length > 0 ? (
          <Dropdown className="w-100 mb-3">
            <Dropdown.Toggle
              variant="none"
              className="d-flex align-items-center btn btn-sm btn-outline-secondary w-100 justify-content-between"
            >
              {selectedAccount ? (
                <div
                  className="d-flex align-items-center flex-grow-1"
                  style={{ width: "calc(100% - 70px)" }}
                >
                  <ProfileImage
                    className={
                      "symbol symbol-28 symbol-circle bg-light flex-shrink-0 me-2"
                    }
                    imageSize={28}
                    symbolClass={
                      "symbol-label text-light-alt fw-bold text-uppercase fs-8"
                    }
                    name={selectedAccount.ConnectedAccounts.accountName}
                    profileUrl={
                      selectedAccount.ConnectedAccounts.profilePicture
                    }
                    networkId={selectedAccount.Network.id}
                  />
                  <div className="overflow-hidden w-100">
                    <p className="fw-medium mb-0 text-ellipsis fs-8 text-start">
                      {selectedAccount.ConnectedAccounts.accountName}
                    </p>
                  </div>
                </div>
              ) : (
                <div className="d-flex align-items-center flex-grow-1">
                  <i className="ri-account-circle-fill icon-1 text-muted me-2"></i>
                  <div className="overflow-hidden w-100">
                    <p className="fw-medium mb-0 text-ellipsis fs-8 text-start">
                      Add Another Account
                    </p>
                  </div>
                </div>
              )}
            </Dropdown.Toggle>

            <Dropdown.Menu className="w-100 navi flex-column navi-hover py-2 connectedAllAccounts">
              <div className="fs-7 px-3 py-2 fw-semibold text-gray-500">
                Select Account:
              </div>
              {accounts && accounts.length
                ? accounts.map((acc) => (
                    <Dropdown.Item
                      className="justify-content-between"
                      key={acc.ConnectedAccounts.accountId}
                      onClick={() => {
                        this.setState({ selectedAccount: acc }, () =>
                          this.onAccountChange(acc.ConnectedAccounts.id)
                        );
                      }}
                    >
                      <div
                        className="d-flex align-items-center flex-grow-1"
                        style={{ width: "calc(100% - 70px)" }}
                      >
                        <ProfileImage
                          className={
                            "symbol symbol-28 symbol-circle bg-light flex-shrink-0 me-2"
                          }
                          imageSize={28}
                          symbolClass={
                            "symbol-label text-light-alt fw-bold text-uppercase fs-8"
                          }
                          name={acc.ConnectedAccounts.accountName}
                          profileUrl={acc.ConnectedAccounts.profilePicture}
                          networkId={acc.Network.id}
                        />
                        <div className="overflow-hidden w-100">
                          <p className="fw-medium mb-0 text-ellipsis fs-8 text-start">
                            {acc.ConnectedAccounts.accountName}
                          </p>
                        </div>
                      </div>
                      <button
                        type="button"
                        className="btn btn-sm btn-danger fs-10 py-1"
                        onClick={() => this.deleteAccount(acc)}
                      >
                        Disconnect
                      </button>
                    </Dropdown.Item>
                  ))
                : null}
              <Dropdown.Item
                className="align-items-center justify-content-between"
                onClick={() => this.onAccountChange()}
              >
                <div className="d-flex align-items-center justify-content-start">
                  <i className="ri-account-circle-fill icon-1 text-muted me-2"></i>
                  <div className="fw-medium fs-8 text-dark">
                    Add Another Account
                  </div>
                </div>
                <i className="ri-add-circle-line icon-3 text-muted ms-2"></i>
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        ) : null}
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    accountId: state.addFeed.selectedAccount,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    selectAccount: (id) => dispatch(selectAccount(id)),
    disconnectAccount: (accountId, callback) =>
      dispatch(disconnectUser(accountId, callback)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AccountPicker);
